<template>
  <div class="animated fadeIn">
    <b-row>
      <b-button class="menu-btn" variant="primary" @click="displayAddShop = !displayAddShop">
        <i class="icon-arrow-up icons font-2xl d-block mt-4 displayAddShopIcon" v-if="displayAddShop"></i>
        <i class="icon-arrow-down icons font-2xl d-block mt-4 displayAddShopIcon" v-else></i>
        添加物品
      </b-button>
    </b-row>
    <b-card v-show="displayAddShop">
      <div slot="header">
        <strong>添加</strong> 物品
      </div>
      <b-form>
        <b-form-group validated
                      label="物品名称">
          <b-form-input id="shopName" type="text" v-model="addShopData.name"></b-form-input>
        </b-form-group>
        <b-form-group validated
                      label="物品描述">
          <b-form-input id="shopNotice" type="text" v-model="addShopData.describe"></b-form-input>
        </b-form-group>
        <b-form-group validated
                      label="价格">
          <b-form-input id="shopNotice" type="text" v-model="addShopData.price"></b-form-input>
        </b-form-group>
        <b-form-group validated
                      label="所属店铺">
          <select name="shopSelect" class="shopSelect" v-model="addShopData.shopId">
            <option v-for="(v,k) in shopList" :value="v.id">{{v.id}}:{{v.name}}</option>
          </select>
        </b-form-group>
        <div slot="footer">
          <b-button type="button" size="sm" variant="primary" @click="addShop"><i class="fa fa-dot-circle-o"></i> 添加</b-button>
          &nbsp;
          <b-button type="button" size="sm" variant="danger" @click="displayAddShop = !displayAddShop"><i class="fa fa-ban"></i> 取消</b-button>
        </div>
      </b-form>
    </b-card>
    <b-row>
      <b-col>
        <table border="0" class="cTable">
          <tr class="table-line1">
            <th>物品id</th>
            <th>物品名称</th>
            <th>物品描述</th>
            <th>店铺名称</th>
            <th>原价</th>
            <th>现价</th>
            <th>价格控制</th>
            <th>操作</th>
          </tr>
          <tr v-for="(v,k) in list">
            <td>{{v.id}}</td>
            <td>{{v.name}}</td>
            <td>{{v.describe}}</td>
            <td>{{v.shopname}}</td>
            <td>{{v.price}}</td>
            <td>{{v.newPrice?v.newPrice:v.price}}</td>
            <td>
              <div v-if="v.newPrice">
                从{{v.priceChangeBeginAt}}到{{v.priceChangeEndAt}}，
                {{v.priceChangeIncOrDec==1?'降价':'涨价'}}，
                {{v.priceChangeType==1?'增减':v.priceChangeType==2?'打折':'直接改价'}}，
                数值：{{v.priceChangePrice}}
                &nbsp;
                <i class="icon-trash icons font-2xl d-block mt-4" @click="delPriceControl(v)"></i>
              </div>
              <div v-else>
                增加一个优惠/涨价机制
                <i class="icon-plus icons font-2xl d-block mt-4" @click="addPriceControl(v)"></i>
              </div>
            </td>
            <td>
              <b-button class="td-btn" variant="primary" @click="delShop(v)">删除物品</b-button>
            </td>
          </tr>
        </table>
      </b-col>
    </b-row>

  </div>

</template>

<script>
    import normalBtn from './normalBtn.vue';

    export default {
        name: 'tables',
        components: {normalBtn},
        data: () => {
            return {
                list: [],
                displayAddShop: false,
                addShopData: {
                    name: '',
                    describe: '',
                    shopId: 0,
                    price: 0
                },

                shopList:[]
            }
        },

        methods: {
            changeValue(key,value){
                this[key] = value;
            },

            listShop() {
                this.$ajax('goods', {
                    method: 'listAllGoods',
                    token: window.localStorage.getItem('token')
                }, (res) => {
                    console.log(res);
                    if (res.code == 0) {
                        this.list = [];
                        for (var i in res.data) {
                            this.list.push(res.data[i]);
                        }
                    } else {
                        alert(res.msg);
                    }
                });
            },

            addShop() {
                console.log(this.addShopData);
                if(confirm("确定要添加"+this.addShopData.name+"吗？")) {
                    this.$ajax('goods',{
                        method: 'addGoods',
                        name: this.addShopData.name,
                        describe: this.addShopData.describe,
                        token: window.localStorage.getItem('token'),
                        price: this.addShopData.price,
                        shopId: this.addShopData.shopId
                    },(res) => {
                        console.log(res);
                        if (res.code == 0) {
                            alert('添加成功');
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    });
                }
            },

            delShop(v) {
                if (confirm("确定要删除以下物品吗？\n\n" + v.name)) {
                    this.$ajax('goods',{
                        method:'deleteGoods',
                        token: window.localStorage.getItem('token'),
                        goodId: v.id
                    }, (res) => {
                        if (res.code == 0) {
                            alert('删除成功');
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    })
                }
            },

            delPriceControl(v){
                alert('将要删除改价机制：'+JSON.stringify(v));
                alert('暂未开放此功能');
            },

            addPriceControl(v){
                alert('将要增加改价机制：'+JSON.stringify(v));
                alert('暂未开放此功能');
            },

            getShopList(){
                this.$ajax('shop', {
                    method: 'listShop',
                    token: window.localStorage.getItem('token')
                }, (res) => {
                    console.log(res);
                    if (res.code == 0) {
                        this.shopList = [];
                        for (var i in res.data) {
                            this.shopList.push(res.data[i]);
                        }
                    } else {
                        alert(res.msg);
                    }
                });
            }
        },

        created() {
            this.listShop();
            this.getShopList();
        }
    }
</script>
<style type="text/css">
  .cTable {
    width: 100%;
  }

  .cTable * {
    text-align: center;
  }

  .table-line1 {
    border-bottom: solid 2px black;
  }

  .td-btn {
    margin: 10px 10px;
  }

  .menu-btn {
    margin: 10px 0 20px 0;
  }

  .icons {
    display: inline !important;
    font-size: 12px !important;
    margin: 0 5px;
    color: blue;
  }

  .displayAddShopIcon {
    color: white;
  }

  .shopSelect{
    width: 100%;
    height: 33px;
  }
</style>