<template>
  <b-button variant="primary" @click="click">{{text}}</b-button>
</template>

<script>
    export default {
        name: 'standard-buttons',
        data: () => {
            return {
                togglePress: false,
            }
        },

        props: [
            'text'
        ],

        methods: {
            click(){
              this.$emit('click');
            }
        }
    }
</script>

<style scoped>
  .btn.disabled {
    cursor: auto;
  }
</style>
